<template>
  <label class="welcome welcome__income">
    <h1>Welcome to Dailybread!</h1>

    <p>Please enter your income at each payday</p>

    <money
      @keyup.native.enter="(e) => $emit('incomeUpdated', e.target.value)"
      v-model="income"
      v-bind="money"
    ></money>
  </label>
</template>

<script>
  import { Money } from 'v-money';

  export default {
    data() {
      return {
        income: this.amountPaid,

        money: {
          decimal: '.',
          thousands: ',',
          prefix: '$',
          suffix: '',
          precision: 2,
          masked: false,
        },
      };
    },

    props: ['amountPaid'],

    components: {
      Money,
    },
  };
</script>

<style lang="scss">
</style>



